import React, { useEffect, useState } from "react";
import PageLayout from "../../layout";
import {
  Banner,
  Button,
  CategoryCard,
  FormGroup,
  Input,
  RandomCard,
  Textarea,
} from "../../containers";
import { hooks } from "../../hooks";
import { useParams } from "react-router-dom";
import styles from "./styles.module.css";
import { timeAgo } from "../../containers/Marque/time";
import {
  FaFacebook,
  FaInstagram,
  FaLinkedinIn,
  FaTwitter,
  FaYoutube,
} from "react-icons/fa";
import { useIsMutating } from "@tanstack/react-query";
import ShareButton from "react-web-share-button";
import { errorAlert, successAlert } from "../../utils";
import avatar from "../../assets/avatar.png";
const SinglePost = () => {
  const posts = hooks.usePost();
  const categories = hooks.useCategory();
  const { id } = useParams();
  const post = posts?.find((x) => x._id === id);

  function removeHTMLTags(htmlString) {
    const doc = new DOMParser().parseFromString(htmlString, "text/html");
    return doc.body.textContent || "";
  }

  function getRandomInt(min, max) {
    return Math.floor(Math.random() * (max - min)) + min;
  }

  const [varRandom, setVarRandom] = React.useState([]);

  useEffect(() => {
    function getRandomItems(arr, numItems) {
      const randomItems = [];
      const totalItems = arr.length;

      while (randomItems.length < numItems) {
        const randomIndex = getRandomInt(0, totalItems);

        if (!randomItems.includes(randomIndex)) {
          randomItems.push(randomIndex);
        }
      }
      const selectedItems = randomItems.map((index) => arr[index]);

      return selectedItems;
    }

    if (posts.length > 5) {
      const randomNewsItems = getRandomItems(posts, 3);
      setVarRandom(randomNewsItems);
    }
  }, [posts]);

  const [formData, setFormData] = useState({});
  const [errors, setErrors] = useState({});

  const handleChange = (name, value) => {
    setFormData({ ...formData, [name]: value });
  };

  const validationHandler = (name, error) => {
    setErrors({ ...errors, [name]: error });
  };

  const isLoading = useIsMutating();

  const { mutate, reset, isSuccess, isError, error } = hooks.useAddComment();
  const submitHandler = () => {
    mutate({ ...formData, post: post?._id });
  };
  console.log(post, "post");

  const someDate = new Date(post?.createdAt);

  const data = hooks.useComment();
  const postComment = data?.filter((x) => x.post === post?._id);

  if (isSuccess) {
    reset();
    successAlert("Created Successfully");
    setFormData("");
  }
  if (isError) {
    reset();
    errorAlert(error);
  }

  console.log(postComment, "comment");
  return (
    <PageLayout>
      <div className={styles.blogFlex}>
        <div className={styles.blog}>
          <h1>{post?.title}</h1>
          <br />
          <div className={styles.userInfo}>
            <div className={styles.dp}>
              <img src={avatar} alt={post?.author} />
            </div>
            <div className={styles.dpName}>
              <p style={{ fontSize: "12px", color: "gray" }}>{post?.author}</p>
              <p style={{ fontSize: "12px", color: "gray" }}>
                {timeAgo(someDate)}
              </p>
            </div>
          </div>

          <Banner image={post?.images?.[0]} />
          <div className={styles.socialMediaContainer}>
            <div className={styles.socialMedia}>
              <a href="https://web.facebook.com/lotusbetaanalytics">
                <FaFacebook />
              </a>
              <a href="https://twitter.com/lotusbeta">
                <FaTwitter />
              </a>
              <a href="https://www.instagram.com/lbannigeria">
                <FaInstagram />
              </a>
              <a href="https://www.linkedin.com/company/lotus-beta-analytics">
                <FaLinkedinIn />
              </a>
              <a href="https://www.youtube.com/channel/UCOhLIAisZyiolylqVhuq2qg">
                <FaYoutube />
              </a>
            </div>
          </div>
          <div
            dangerouslySetInnerHTML={{ __html: removeHTMLTags(post?.content) }}
            className={styles.contents}
          />
          <div className={`btnContainer ${styles.shareBtn}`}>
            <ShareButton
              title={post?.title}
              text={`Share ${post?.title}`}
              url={window.location.href}
            />
          </div>
          <br />
          <br />
          {postComment?.map((item, i) => (
            <div className={styles.commentCard} key={i}>
              <div className={styles.userInfo}>
                <div className={styles.dp}>
                  <img src={avatar} alt={item?.name} />
                </div>
                <div className={styles.dpName}>
                  <h5>{item?.name}</h5>
                  <p>{new Date(item?.createdAt)?.toDateString()}</p>
                </div>
              </div>
              <div className={styles.userComment}>
                <div className={styles.spacing}></div>
                <div>
                  <em>{item?.text}</em>
                </div>
              </div>
            </div>
          ))}
          <br />
          <br />
          <div className={styles.comment}>
            <div className={styles.titleComment}>
              <h4>Leave a Reply</h4>
              <br />
              <p>
                Your email address will not be published. Required fields are
                marked *
              </p>{" "}
              <br />
            </div>

            <FormGroup
              onSubmit={submitHandler}
              validation={formData}
              errors={errors}
              setErrors={setErrors}
            >
              <Input
                name="name"
                label="Your Name"
                value={formData["name"]}
                onChange={handleChange}
                type="text"
                validationHandler={validationHandler}
                error={errors.name}
                required={true}
              />
              <Input
                name="email"
                label="Email Address"
                value={formData["email"]}
                onChange={handleChange}
                type="email"
                validationHandler={validationHandler}
                error={errors.email}
                required={true}
              />

              <Textarea
                name="text"
                label="Your Comment"
                value={formData["text"]}
                onChange={handleChange}
                type="password"
                validationHandler={validationHandler}
                error={errors.text}
                required={true}
                size="large"
              />
              <Button
                title={"Post Comment"}
                loading={Boolean(isLoading)}
                disabled={Boolean(isLoading)}
                bgColor="btnBlack"
              />
            </FormGroup>
          </div>
        </div>
        <div className={styles.randomBlog}>
          <div className={styles.fixedItems}>
            {varRandom?.map((item, i) => (
              <RandomCard post={item} key={i} />
            ))}
            {/* categories */}
            <div className={styles.categories}>
              <h6>Categories</h6>
              <div className={styles.categoriesFlex}>
                {categories
                  ?.map((item, i) => <CategoryCard category={item} key={i} />)
                  .slice(0, 3)}
              </div>
            </div>
          </div>
        </div>
      </div>
    </PageLayout>
  );
};

export default SinglePost;
